.clickerComponent {
  padding: 0 4em;
}

@mixin image {
  width: 100vw;
  max-width: 500px;
  border-radius: 50%;
  cursor: pointer;
  background-color: transparent;
  user-select: none;
}

.clicker {
  @include image
}

.pulse {
  @include image;
  transition: transform 0.1s, filter 0.2s;
  animation: pulse-animation 0.2s ease-in-out;
}

@keyframes pulse-animation {
  0% {
    transform: scale(1);
    filter: brightness(100%);
  }
  50% {
    transform: scale(1.05);
    filter: brightness(120%);
  }
  100% {
    transform: scale(1);
    filter: brightness(100%);
  }
}
